<template>
  <div class="userData" @click="closed">
    <mt-header title="">
      <span slot="left" @click="back">
        <mt-button icon="back"></mt-button>
      </span>
    </mt-header>
    <section class="info-box mb-10">
      <div class="info-row">
        <span class="info-row-title"></span>
        <span class="avator-sm">
          <img :src="photo" alt />
        </span>
      </div>
      <div class="info-row">
        <span class="info-row-title"></span>
        <input @blur="nickMsg" class="info-row-text" type="text" maxlength="12" v-model="nickname" />
      </div>
      <div class="info-row">
        <span class="info-row-title"></span>
        <span class="info-row-text" v-text="gender"></span>
        <div class="info-row-right info-right-sex">
          <span :class="gender1?'sex-active':''" @click="changeSex(1)"></span>
          <span :class="gender0?'sex-active':''" @click="changeSex(0)"></span>
        </div>
      </div>
      <div class="info-row">
        <span class="info-row-title"></span>
        <input @blur="emailMsg" class="info-row-text" type="email" v-model="email" />
      </div>
      <div class="info-row" @click.stop="pickerValue">
        <span class="info-row-title"></span>
        <span class="info-row-text" v-text="age"></span>
        <mt-datetime-picker
          type="date"
          ref="picker"
          year-format="{value} "
          month-format="{value} "
          date-format="{value} "
          @confirm="handleConfirm"
          :startDate="startDate"
        ></mt-datetime-picker>
      </div>

      <div class="info-row" @click.stop="ShouPup">
        <span class="info-row-title"></span>
        <span class="info-row-text" v-text="address" />
        <!--         <span class="info-row-text">,</span> -->
        <mt-popup
          v-model="popupVisible"
          popup-transition="popup-fade"
          modal="true"
          closeOnClickModal="true"
          position="bottom"
        >
          <mt-picker :slots="slots" @change="onValuesChange" style="width: 7.6rem;" showToolbar>
            <div class="picker-toolbar-title">
              <div class="usi-btn-cancel" @click="cancle"></div> 
              <div class></div>
              <div class="usi-btn-sure" @click="editSave"></div>
            </div>
          </mt-picker>
        </mt-popup>
      </div>
      <div class="info-row">
        <span class="info-row-title"></span>
        <input class="info-row-text" type="text" v-model="sign" />
      </div>
      <div class="info-row">
        <span class="info-row-title"></span>
        <input
          @blur="pwMsg"
          class="info-row-text"
          type="password"
          minlength="6"
          maxlength="12"
          v-model="password"
        />
      </div>

      <mt-button size="large" @click="changeData"></mt-button>
    </section>
  </div>
</template>
<script>
import { city, privinceList, cityList, districtList } from "../assets/add.js";
import Qs from "qs";
export default {
  data() {
    return {
      isLogin: false,
      gender: "",
      gender1: false,
      gender0: false,
      nickname: "",
      age: "",
      address: "",
      sign: "",
      photo: "",
      email: "",
      password: "",
      popupVisible: false,
      startDate: new Date("1968-01-01"),
      myprivinceList: [], //
      mycityList: [], //
      mydistrictList: [], //
      areapicker: "",
      nickpass: 1,
      pwpass: 1,
      emailpass: 1,
      slots: [
        {
          flex: 1,
          values: privinceList, //
          className: "slot1",
          textAlign: "center"
        },
        {
          pider: true,
          content: "-",
          className: "slot2"
        },
        {
          flex: 1,
          values: cityList,
          className: "slot3",
          textAlign: "center"
        },
        {
          pider: true,
          content: "-",
          className: "slot4"
        },
        {
          flex: 1,
          values: districtList,
          className: "slot5",
          textAlign: "center"
        }
      ],
      myAddressPrivince: "", //
      myAddressCity: "", //
      myAddressDistrict: "", //
      showToolbar: true
    };
  },
  watch: {
    myAddressPrivince(oldval, newval) {
      //
      this.areapicker.setSlotValues(2, this.mycityList);
      this.areapicker.setSlotValue(2, this.mycityList[0]);
    },
    myAddressCity(oldval, newval) {
      //
      this.areapicker.setSlotValues(4, this.mydistrictList);
      this.areapicker.setSlotValue(4, this.mydistrictList[0]);
    },
    myAddressDistrict(oldval, newval) {}
  },
  methods: {
    pickerValue() {
      this.$refs.picker.open();
    },
    closed() {
      this.$refs.picker.close();
      this.popupVisible = false;
    },
    handleConfirm(data) {
      this.age =
        data.getFullYear() +
        "" +
        (data.getMonth() + 1) +
        "" +
        data.getDate() +
        "";
      this.$refs.picker.close();
    },
    back() {
      this.$router.go(-1);
    },
    quit() {
      this.$messagebox
        .confirm("")
        .then(action => {
          localStorage.removeItem("Flag");
          localStorage.removeItem("userid");
          this.$store.dispatch("userid", undefined);
          this.$router.push("/login");
        })
        .catch(err => {});
    },
    changeSex(n) {
      if (n == 1) {
        this.gender = "";
        this.gender1 = true;
        this.gender0 = false;
      } else {
        this.gender = "";
        this.gender1 = false;
        this.gender0 = true;
      }
    },
    ShouPup() {
      this.popupVisible = true;
    },
    cancle() {
      console.log(123);
      this.popupVisible = false;
       this.$refs.picker.close();
    }, 
    editSave(oldval, newval) {
      //
      this.areapicker.setSlotValues(2, this.mycityList);
      this.areapicker.setSlotValue(2, this.mycityList[0]);
      //
      this.areapicker.setSlotValues(4, this.mydistrictList);
      this.areapicker.setSlotValue(4, this.mydistrictList[0]);
      this.address =
        this.myAddressPrivince + this.myAddressCity + this.myAddressDistrict;
      this.popupVisible = false;
    },
    onValuesChange(picker, values) {
      this.areapicker = picker;
      this.mycityList = [];
      this.mydistrictList = [];
      var chooseList = city.filter(function(item) {
        return item.name == values[0];
      });
      if (chooseList[0].sub) {
        for (var item of chooseList[0].sub) {
          this.mycityList.push(item.name);
        }
        //
        if (chooseList[0].sub.length > 1) {
          var choosedisList = [];
          if (this.mycityList.indexOf(values[2]) > -1 && values[2] != "") {
            choosedisList = chooseList[0].sub.filter(function(item) {
              return item.name == values[2];
            });
            for (var item of choosedisList[0].sub) {
              this.mydistrictList.push(item.name);
            }
          } else {
            this.mydistrictList = [];
          }
        }
        //
        else {
          for (var item of chooseList[0].sub[0].sub) {
            this.mydistrictList.push(item.name);
          }
        }
      }
      this.myAddressPrivince = values[0];
      this.myAddressCity = values[2];
      this.myAddressDistrict = values[4];
    },
    changeData() {
      if (this.pwpass == 1 && this.nickpass == 1 && this.emailpass == 1) {
        var userid = localStorage.getItem("userid");
        var gender;
        if (this.gender == "") {
          gender = 1;
        } else {
          gender = 0;
        }
        var data = {
          address: this.address,
          nickname: this.nickname,
          gender,
          age: this.age,
          sign: this.sign,
          userid,
          email: this.email,
          password: this.password
        };
        if (userid) {
          this.axios({
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            },
            method: "post",
            url: "/travel/changeData",
            data: Qs.stringify(data)
          }).then(result => {
            console.log(result.data);
            if (result.data.code == "1") {
              this.$toast("");
              this.$router.push("/user");
            } else if (result.data.code == "-1") {
              this.$toast("");
            }
          });
        }
      } else if (this.nickpass !== 1) {
        this.$toast("");
      } else if (this.pwpass !== 1) {
        this.$toast("612");
      } else if (this.emailpass !== 1) {
        this.$toast("");
      }
    },
    pwMsg() {
      if (!this.password.trim()) {
        //this.$toast("");
        this.pwpass = 0;
      } else if (!/^\w{6,12}$/.test(this.password)) {
        //this.$toast("612");
        this.pwpass = 0;
      } else {
        this.pwpass = 1;
      }
    },
    nickMsg() {
      if (!this.nickname.trim()) {
        //this.$toast("");
        this.nickpass = 0;
      } else {
        this.nickpass = 1;
      }
    },
    emailMsg() {
      if (
        !/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(
          this.email
        )
      ) {
        //this.$toast("");
        this.emailpass = 0;
      } else {
        this.emailpass = 1;
      }
    },
    load(){
      
    }
  },
  created() {
    //
    if (localStorage.getItem("userid")) {
      this.$store.dispatch("userData");
      var userData = this.$store.getters.userData;
      console.log(userData.userData);
      var {
        nickname,
        gender,
        age,
        address,
        sign,
        photo,
        email,
        password
      } = userData.userData;
      console.log(nickname, gender, age, address, sign, photo);
      this.nickname = nickname;
      this.age = age;
      this.address = address;
      this.sign = sign;
      this.photo = this.host+photo;
      this.email = email;
      this.password = password;
      if (gender == "1") {
        this.gender = "";
        this.gender1 = true;
        this.gender0 = false;
      } else {
        this.gender = "";
        this.gender1 = false;
        this.gender0 = true;
      }
    }
  }
};
</script>
<style scoped>
.v-enter-active,
.v-leave-active {
  transition: all 0.3s;
}
.v-enter,
.v-leave-to {
  transform: translateX(100%);
}
.userData {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 101;
  background-color: #f9f9f9;
}

.mint-header {
  background-color: #f7c902;
  margin-bottom: 0.2rem;
}

/**/
.info-row {
  position: relative;
  height: 1.04rem;
  line-height: 1.04rem;
  padding-left: 0.4rem;
  border-bottom: 1px solid #f0ebeb;
}
.info-row span {
  vertical-align: middle;
}
.info-row input {
  font-weight: 600;
  color: #474747;
  background: #f9f9f9;
}
.info-row-title {
  display: inline-block;
  width: 1.4rem;
  font-size: 0.28rem;
  color: #b8b8b8;
  text-align: left;
}
.avator-sm {
  display: inline-block;
  width: 0.66rem;
  height: 0.66rem;
  line-height: 100%;
  border-radius: 0.66rem;
  overflow: hidden;
}
.avator-sm img {
  width: 100%;
  height: 100%;
}
.info-row-text {
  font-size: 0.28rem;
  color: #474747;
}
.info-right-sex span {
  display: inline-block;
  width: 0.64rem;
  height: 0.42rem;
  line-height: 0.42rem;
  text-align: center;
  background: #efefef;
  border-radius: 0.06rem;
  font-size: 0.28rem;
  color: #ffffff;
}
.sex-active {
  background: #f7c902 !important;
}
.info-sex-man {
  margin-right: 0.25rem;
}
.info-row-right {
  position: absolute;
  top: 0;
  right: 0.36rem;
}
.info-row-right img {
  width: 0.12rem;
  height: 0.22rem;
}
.info-right-phone span {
  display: inline-block;
  width: 1.46rem;
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.28rem;
  color: #ffffff;
  background: #f7c902;
  text-align: center;
  border-radius: 0.06rem;
  font-weight: 500;
}
.mint-button--large {
  margin: 0.5rem auto;
  width: 50%;
  background: #f7c902;
  color: #fff;
  border-radius: 0.1rem;
}
.picker-toolbar-title {
  display: flex;
  justify-content: space-between;
  height: 45px;
  font-size: 0.3rem;
  margin: 0 0.5rem;
}
.usi-btn-cancel {
  color: #666;
}
.usi-btn-sure {
  color: #1e83d3;
}
</style>
