import Vue from 'vue'
import App from './App.vue'
import router from './router'

//mint-ui
import mintui from 'mint-ui'
import 'mint-ui/lib/style.css'
import { MessageBox } from 'mint-ui'
Vue.use(mintui)


//axios
import axios from 'axios'
axios.defaults.withCredentials = true
axios.defaults.baseURL = "http://gotravel.applinzi.com/"
Vue.prototype.axios = axios
//vuex
import Vuex from 'vuex'
Vue.use(Vuex)
//vue-touch
import VueTouch from 'vue-touch'
Vue.use(VueTouch, {name: 'v-touch'})

Vue.prototype.host = "http://gotravel.applinzi.com/"

var store = new Vuex.Store({
  //vuex
  state: {
    userInfo: null, //
    isLogin: false,  //
    token: '',
    isliked: 0,      //
    isCollect: false,    //
    agreen: [],
    userid: localStorage.getItem("userid"),     //id
    userData: { userData: '' }
  },
  //vuex
  getters: {
    isLogin(state) {
      return state.isLogin
    },
    userid(state) {
      return state.userid
    },
    userData(state) {
      return state.userData
    },
    isCollect(state) {
      return state.isCollect
    }
  },
  //vuex
  mutations: {
    //
    userStatus(state, flag) {
      state.isLogin = flag
    },
    //ID
    userid(state, num) {
      state.userid = num
    },
    //
    isCollect(state){
      state.isCollect = true
    },
    //
    disCollect(state){
      state.isCollect = false
    }
  },
  //vuexAPIactionsactionsAction 
  //mutationsactions
  //contextcontextstorecontext.commit(' ')
  actions: {
    //
    userLogin({ commit }, flag) {
      commit("userStatus", flag)
    },
    userid({ commit }, num) {
      commit("userid", num)
    },
    userData(context) {
      let userid = context.state.userid
      axios.get('/travel/user', {
        params: {
          userid
        }
      }).then(result => {
        context.state.userData.userData = result.data.result[0]
        //console.log(context.state.userData)
      })
    }
  }
})

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  //
  let getFlag = localStorage.getItem("Flag");
  //isLogin
  if (getFlag === "isLogin") {
    //vuex
    store.state.isLogin = true
    next()

    //
  } else {
    //
    if (to.meta.isLogin) {
      MessageBox
        .confirm("", "")
        .then(action => {
          next({
            path: '/login',
          })
        })
        .catch(err => { });
      // Toast('')
      //
    } else {
      next()
    }
  }
});



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')